import React from 'react';
import { useHistory } from 'react-router-dom';
import 'scss/components/sidebar.scss';
import { SidebarItem } from './SidebarItem';

const OVERVIEW_LABEL = 'Overview';
const INVENTORY_LABEL = 'Inventory';
const ACTIVITY_LABEL = 'Activity';
const ACCOUNT_SETTINGS_LABEL = 'Account Settings';

export const OVERVIEW_ROUTE = '/myaccount/overview';
export const INVENTORY_ROUTE = '/myaccount/inventory';
export const ACTIVITY_ROUTE = '/myaccount/activity';
export const ACCOUNT_SETTINGS_ROUTE = '/myaccount/account_settings';

export const Sidebar = () => {
  const history = useHistory();
  const { location } = history;

  const selectedTab = (e) => {
    return e === location.pathname;
  };

  const handleClick = (e) => {
    history.push(e);
  };

  return (
    <div className="sidebar">
      <div className="sidebar__info">
        <img src="https://picsum.photos/id/237/200/300" alt="avatar-user" />
        <div className="sidebar__info-welcome">
          <span>Hello, </span>
          <span className="strong">Marquez</span>
        </div>
        <div className="sidebar__info-email">marquezz@gmail.com</div>
      </div>
      <div className="sidebar__items-container">
        <SidebarItem
          icon={<i className="pi pi-wallet" />}
          label={OVERVIEW_LABEL}
          active={selectedTab(OVERVIEW_ROUTE)}
          onClick={() => handleClick(OVERVIEW_ROUTE)}
        />
        <SidebarItem
          icon={<i className="pi pi-wallet" />}
          label={INVENTORY_LABEL}
          active={selectedTab(INVENTORY_ROUTE)}
          onClick={() => handleClick(INVENTORY_ROUTE)}
        />
        <SidebarItem
          icon={<i className="pi pi-wallet" />}
          label={ACTIVITY_LABEL}
          active={selectedTab(ACTIVITY_ROUTE)}
          onClick={() => handleClick(ACTIVITY_ROUTE)}
        />
        <SidebarItem
          icon={<i className="pi pi-wallet" />}
          label={ACCOUNT_SETTINGS_LABEL}
          active={selectedTab(ACCOUNT_SETTINGS_ROUTE)}
          onClick={() => handleClick(ACCOUNT_SETTINGS_ROUTE)}
        />
      </div>
    </div>
  );
};
