import { Dropdown } from 'primereact/dropdown';
import { InputText } from 'primereact/inputtext';
import React, { useCallback, useEffect, useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import { tokenSAEService } from 'services/tokenSAEService';
import { tokenASGService } from 'services/tokenASGService';
import '../../scss/components/deposit.scss';
import { Toast } from 'primereact/toast';
import { Dialog } from 'primereact/dialog';
import { utils } from 'ethers';
import { useWallet } from 'hooks/useWallet';

export const Deposit = ({ visible, onHide }) => {
  const tokenSAE = 'SAE';
  const tokenASG = 'ASG';

  const options = [
    { label: tokenSAE, value: tokenSAE },
    { label: tokenASG, value: tokenASG },
  ];
  const { getAddress } = useWallet();

  const account = useSelector((state) => state.authReducer.account);
  const [amount, setAmount] = useState(0);
  const [balance, setBalance] = useState(0);
  const toast = useRef(null);

  const [selectedToken, setSelectedToken] = useState(tokenSAE);

  const getBalance = useCallback(async () => {
    if (selectedToken === tokenSAE) {
      const _balance = await tokenSAEService.balanceOf(account);
      setBalance(_balance);
    } else {
      const _balance = await tokenASGService.balanceOf(account);
      setBalance(_balance);
    }
  }, [account, selectedToken]);

  useEffect(() => {
    if (account) {
      getBalance();
    }
  }, [account, getBalance]);

  const handleMax = () => {
    setAmount(balance);
  };

  const handleTransfer = useCallback(async () => {
    const _rs = await tokenSAEService.transfer(amount);
    if (_rs) {
      toast.current.show({ severity: 'success', summary: 'Transfer', detail: 'Transfer success' });
      getBalance();
      setAmount();
    }
  }, [amount]);

  const onAssetChanged = (e) => {
    console.log(e.value);
    setSelectedToken(e.value);
    getBalance();
    setAmount(0);
  };

  return (
    <Dialog
      visible={visible}
      onHide={onHide}
      closable={false}
      dismissableMask={true}
      header="Deposit"
      className="deposit"
      draggable={false}
    >
      <div className="deposit__container">
        <div className="deposit__content">
          <div className="deposit__transfer">
            <div className="transfer">
              <div className="transfer__label">From</div>
              <div className="transfer__item">
                <div className="wallet__icon metamask"></div>
                <div className="wallet__info">
                  <div className="info__name">Metamask</div>
                  <div className="info__address">{getAddress(account)}</div>
                </div>
              </div>
            </div>
            <div className="transfer__icon">
              <div className="icon-arrow"></div>
            </div>
            <div className="transfer">
              <div className="transfer__label">To</div>
              <div className="transfer__item">
                <div className="wallet__icon icon-app"></div>
                <div className="wallet__info">
                  <div className="info__name">Summoners</div>
                </div>
              </div>
            </div>
          </div>
          <div className="deposit__asset">
            <h6>Asset</h6>
            <Dropdown className="asset-dropdown" options={options} value={selectedToken} onChange={onAssetChanged} />
          </div>
          <div className="deposit__amount">
            <div>
              <h6 className="amount-label">Amount</h6>
              <h6 className="amount-total">{`${balance} BNB`}</h6>
            </div>

            <div className="p-inputgroup">
              <InputText
                className="deposit-input"
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
                placeholder="Amount"
              />
              <div onClick={handleMax} className="max">
                MAX
              </div>
            </div>
          </div>
          <div className="transfer__button">
            <div className="button-next" onClick={handleTransfer}>
              Next
            </div>
          </div>
        </div>
      </div>
      <Toast ref={toast}></Toast>
    </Dialog>
  );
};
