import { Dropdown } from 'primereact/dropdown';
import React, { useState } from 'react';
import 'scss/components/inventory.scss';

const TabView = ({ selected, setSelected }) => {
  return (
    <ul className="cus-tabview">
      <li className={`cus-tabview__panel ${selected === 1 && 'active'}`} onClick={() => setSelected(1)} aria-hidden>
        Heroes
      </li>
      <li className={`cus-tabview__panel ${selected === 2 && 'active'}`} onClick={() => setSelected(2)} aria-hidden>
        Items
      </li>
      <li className={`cus-tabview__panel ${selected === 3 && 'active'}`} onClick={() => setSelected(3)} aria-hidden>
        Lands
      </li>
      <li className={`cus-tabview__panel ${selected === 4 && 'active'}`} onClick={() => setSelected(4)} aria-hidden>
        Guilds
      </li>
      <li className={`cus-tabview__panel ${selected === 5 && 'active'}`} onClick={() => setSelected(5)} aria-hidden>
        Costumes
      </li>
    </ul>
  );
};

const cities = [
  { name: 'Highest ID', code: 'HI' },
  { name: 'Lowest ID', code: 'LI' },
  { name: 'Highest Price', code: 'HP' },
  { name: 'Lowest Price', code: 'LP' },
  { name: 'Highest Star', code: 'HS' },
  { name: 'Lowest Star', code: 'LS' },
  { name: 'Recently Listed', code: 'RL' },
  { name: 'Recently Sold', code: 'RS' },
  { name: 'Recently Offered', code: 'RO' },
];

export const Inventory = () => {
  const [selected, setSelected] = useState(1);
  const [selectedCity1, setSelectedCity1] = useState(null);

  const onCityChange = (e) => {
    setSelectedCity1(e.value);
  };

  return (
    <React.Fragment>
      <div className="inventory-page">
        <div className="inventory-page__cards">
          <div className="p-col-4 inventory-tab-card">
            <div className="inventory-tab-card__bnb-icon" />
            <div className="inventory-tab-card__content">0 BNB</div>
          </div>
          <div className="p-col-4 inventory-tab-card">
            <div className="inventory-tab-card__bnb-icon" />
            <div className="inventory-tab-card__content">0 SAE</div>
          </div>
          <div className="p-col-4 inventory-tab-card">
            <div className="inventory-tab-card__bnb-icon" />
            <div className="inventory-tab-card__content">0 ASG</div>
          </div>
        </div>
        <div className="inventory-page__tabs">
          <TabView selected={selected} setSelected={setSelected} />
        </div>
        <div className="inventory-page__content">
          <div className="filter-header p-d-flex p-jc-between">
            <Dropdown
              value={selectedCity1}
              options={cities}
              optionLabel="name"
              placeholder="Sort By"
              onChange={onCityChange}
            />
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};
